<template>
  <div class="nav-wrap">
    <div @click="$emit('click-left')" class="left-nav">
      <slot name="left">
        <svg class="icon" aria-hidden="true" slot="prepend" v-if="leftArrow" :class="leftArrowDisabled ? 'light-arr' : ''">
          <use xlink:href="#icon-jiantou"></use>
        </svg>
        <span
          class="left-text"
          v-if="leftText"
          @click.stop
          v-text="leftText"
        />
      </slot>
    </div>
    <div>
      <slot name="title">{{ title }}</slot>
    </div>
    <div class="right-nav" @click="$emit('click-right')">
      <slot name="right">
        <span
          v-if="rightText"
          v-text="rightText"
        />
      </slot>
    </div>
  </div>
</template>

<script>
export default {
  name: 'NavBar',
  props: {
    title: String,
    fixed: Boolean,
    leftText: String,
    rightText: String,
    leftArrow: Boolean,
    leftArrowDisabled: {
      type: Boolean,
      default: false,
    },
    border: {
      type: Boolean,
      default: true,
    },
    zIndex: {
      type: Number,
      default: 1,
    },
  },
  computed: {
    style() {
      return {
        zIndex: this.zIndex,
      };
    },
  },
};
</script>

<style lang="stylus" scoped>
  @import "../assets/styles/common.styl";
 .nav-wrap {
   h(44px);
   font-size: $size-large;
   color: $color-dark-gray;
   border-bottom: 1px solid $border-color;
   display: flex;
   justify-content: space-between;
   .icon {
     color: $color-gray;
    }
    .light-arr {
      color: $border-color;
    }
    .left-nav,.right-nav {
      width: 30px;
    }

    .left-text {
      color: $color-light-gray;
      font-size: $size-normal;
      position: absolute;
      left: 50px;
      max-width: 70px;
      overflow:hidden;
      text-overflow: ellipsis;
      white-space: nowrap;
    }
 }
</style>
