<template>
  <div>
    <NavBar :title="$t('common.faceRecognitionSystem')"
      @click-right="showPopup('myPopup')"
      :leftArrow="true"
      class="nav-bar"
      @click-left="goBack"
      :left-text="peopleName"
      :leftArrowDisabled="isHome">
      <svg class="icon right" aria-hidden="true" slot="right">
        <use xlink:href="#icon-menu"></use>
      </svg>
    </NavBar>
    <div class="content">
      <slot></slot>
    </div>

<cube-popup type="my-popup" ref="myPopup" position="bottom" :maskClosable="true" class="pop-wraper">
  <ul class="menu">
    <li @click="logOut">{{$t('common.logout')}}</li>
    <li @click="goModify">{{$t('common.modifyPwd')}}</li>
    <!-- <li @click="toggleLang">{{$t('common.toggleLang')}}</li> -->
    <!-- <li @click="goBindPhone">{{telephone ? $t('common.modifyPhone') : $t('common.bindPhone')}}</li> -->
  </ul>
  <div class="cancel" @click="() => hidePop('myPopup')">{{$t('common.cancel')}}</div>
</cube-popup>

<cube-popup type="my-popup" ref="toggleLang" position="bottom" :maskClosable="true" class="pop-wraper">
  <ul class="menu">
    <li @click="() => onSelectLang(item, index)" v-for="(item, index) in langMenu" :key="index">{{item.content}}</li>
  </ul>
  <div class="cancel" @click="() => hidePop('toggleLang')">{{$t('common.cancel')}}</div>
</cube-popup>

  </div>
</template>

<script>
import NavBar from '@/components/NavBar.vue';
import CubeUI from 'cube-ui';
import enUSMessages from 'cube-ui/src/locale/lang/en-US';
import zhCNMessages from 'cube-ui/src/locale/lang/zh-CN';
// 自己引入繁体中文语言包
import zhTWMessages from '../locales/cube-ui/zh_TW';

const Locale = CubeUI.Locale;
const apis = {
  logout: '/h5/logout',
};

export default {
  data() {
    return {};
  },
  components: {
    NavBar,
  },
  methods: {
    showPopup(refId) {
      const component = this.$refs[refId];
      component.show();
    },
    hidePop(refName) {
      this.$refs[refName].hide();
    },
    logOut() {
      this.http.post(apis.logout).then((res) => {
        if (res.code === 1000) {
          this.$storage.removeItem('user', false);
          this.$router.push('login');
        } else {
          this.showToastType(res.msg ? `${res.msg}${res.reason ? `:${res.reason}` : ''}` : this.$t('common.logoutFailed'));
        }
      }).catch(() => {
        this.showToastType(this.$t('common.logoutFailed'));
      });
    },
    goModify() {
      this.$router.push('modify-pwd');
    },
    goBindPhone() {
      this.$router.push('bind-phone');
    },
    showToastType(txt, type = 'error') {
      const toast = this.$createToast({
        txt,
        type,
      });
      toast.show();
    },
    goBack() {
      if (!this.isHome) {
        this.$router.go(-1);
      }
    },
    toggleLang() {
      this.hidePop('myPopup');
      this.$refs.toggleLang.show();
    },
    onSelectLang(item) {
      const lang = item.value;
      localStorage.setItem('lang', lang);
      switch (lang) {
        case 'en-US':
          Locale.use('en-US', enUSMessages);
          break;
        case 'zh-TW':
          Locale.use('zh-TW', zhTWMessages);
          break;
        default:
          Locale.use('zh-CN', zhCNMessages);
      }
      this.$parent.$i18n.locale = lang;
      this.$refs.toggleLang.hide();
    },
  },
  computed: {
    isHome() {
      const { name: currentRouteName } = this.$router.currentRoute;
      return currentRouteName === 'home';
    },
    user() {
      return this.$storage.getItem('user', false) || {};
    },
    peopleName() {
      return this.user.peopleName || '';
    },
    telephone() {
      return this.user.telephone || '';
    },
    langTitle() {
      return this.$t('common.toggleLang');
    },
    langMenu() {
      return [
        {
          content: this.$t('common.zhCN'),
          value: 'zh-CN',
        },
        {
          content: this.$t('common.zhTW'),
          value: 'zh-TW',
        },
        {
          content: this.$t('common.en'),
          value: 'en-US',
        },
      ];
    },
  },
};
</script>

<style lang="stylus" scoped>
@import "../assets/styles/common.styl";
  .content {
    padding: 33px 16px 10px;
  }
  .right {
    margin-right: 12px;
  }
  .menu {
    background-color: $color-white;
    border-bottom: 10px solid #efefef;
    li {
      border-bottom: 1PX solid $border-color;
      h(42px);
      font-size: $size-middle;
      color: $color-dark-gray;
      &:last-child {
        border: none;
      }
    }
  }
  .icon {
    color: $color-gray;
  }

  .cancel {
    h(42px);
    font-size: $size-middle;
    color: $color-light-gray;
  }

  .pop-wraper {
    >>> .cube-popup-content {
      background-color: $color-white;
    }
  }
</style>
