<template>
  <div>
    <HomeContainer>
      <DateRangePicker v-model="date" :showClear="false" :showToday="false" @input="onGetData" class="date-radius"></DateRangePicker>

      <div class="view-wrapper">
          <cube-scroll ref="scroll" :data="dataList" :options="options" @pulling-down="onPullingDown" @pulling-up="onPullingUp" class="scroll-list">
            <!-- 列表 -->
            <template v-if="dataList.length">
            <div class="record-item" v-for="(item) in dataList" :key="item.id">
              <div class="avatar" :style="{backgroundImage: 'url(' + (item.search_image || '') + ')'}"></div>
              <time class="time">{{ item.recognition_time | dateformat('YYYY-MM-DD HH: mm')}}</time>
            </div>
            </template>
            <template v-else>
              <!-- 空数据 -->
              <NoData />
            </template>
        </cube-scroll>
      </div>

      <cube-tab-bar v-model="selectedLabel" class="tabbar-wrap" @click="toggleTabHandler">
        <cube-tab v-for="(item) in tabs" :label="item.label" :key="item.value" :value="item.value">
        </cube-tab>
      </cube-tab-bar>

    </HomeContainer>
  </div>
</template>

<script>
import HomeContainer from '@/components/HomeContainer.vue';
import DateRangePicker from '@/components/DateRangePicker.vue';
import NoData from '@/components/NoData.vue';
import { formatStartDate, formatEndDate } from '../utils/utils';

const apis = {
  records: '/h5/records',
};

export default {
  data() {
    return {
      date: [new Date(), new Date()],
      pageSize: 50,
      totalNumber: 1,
      totalPage: 1,
      pageNumber: 0,
      selectedLabel: 'record',
      dataList: [],
    };
  },
  components: {
    DateRangePicker,
    HomeContainer,
    NoData,
  },
  methods: {
    onGetData() {
      this.totalNumber = 1;
      this.totalPage = 1;
      this.pageNumber = 1;
      this.loadData(true);
    },
    toggleTabHandler(label) {
      if (label !== this.selectedLabel) {
        this.$router.push({ name: label });
      }
    },
    // 获取数据
    loadData(replace) {
      const {
        pageNumber, pageSize, date,
      } = this;
      const [begin, end] = date;
      const pageData = {
        page_number: pageNumber,
        page_size: pageSize,
      };
      const formData = {
        begin: formatStartDate(begin),
        end: formatEndDate(end),
        ...pageData,
      };
      this.http.get(apis.records, {
        params: formData,
      }).then((res) => {
        if (res.code === 1000) {
          const items = res.data.records;
          this.totalNumber = res.data.total_number;
          this.pageNumber = res.data.page_number;
          this.totalPage = Math.ceil(this.totalNumber / this.pageSize);
          this.dataList = replace ? items : [...this.dataList, ...items];
        } else {
          this.$refs.scroll.forceUpdate();
          console.log(`${res.msg}${res.reason ? `:${res.reason}` : ''}`);
        }
      }).catch((e) => {
        if (this.$refs.scroll) {
          this.$refs.scroll.forceUpdate();
        }
        console.log(e);
      });
    },
    // 触发下拉刷新
    onPullingDown() {
      // console.log('下拉刷新');
      this.totalNumber = 1;
      this.totalPage = 1;
      this.pageNumber = 1;
      this.loadData(true);
    },
    // 触发上拉加载
    onPullingUp() {
      // console.log('上拉加载');
      if (this.totalPage <= this.pageNumber) {
        this.$refs.scroll.forceUpdate();
      } else {
        this.pageNumber = this.pageNumber + 1;
        this.loadData();
      }
    },
  },
  mounted() {
    // 挂载
    this.loadData();
  },
  computed: {
    // 列表加载选项，空数据时只有下拉刷新而无上拉加载
    options() {
      const { pullDownRefresh, pullUpLoad } = this;
      return this.dataList.length ? { pullDownRefresh, pullUpLoad } : { pullDownRefresh };
    },
    tabs() {
      return [{
        label: this.$t('common.visitorManage'),
        value: 'home',
      }, {
        label: this.$t('common.myRecognition'),
        value: 'record',
      }];
    },
    pullUpLoad() {
      return {
        threshold: 0,
        txt: {
          more: this.$t('common.pullUptoLoadMore'),
          noMore: this.$t('common.noMoreData'),
        },
      };
    },
    pullDownRefresh() {
      return {
        threshold: 90,
        stop: 40,
        txt: this.$t('common.refreshSuccess'),
      };
    },
  },
};
</script>

