import Vue from 'vue';
import Router from 'vue-router';
import NameSpace from './NameSpace.vue';
import Home from './views/Home.vue';
import Record from './views/Record.vue';
import ModifyPwd from './views/ModifyPwd.vue';
import BindPhone from './views/BindPhone.vue';

Vue.use(Router);

const router = new Router({
  routes: [
    {
      path: '/app/:id',
      component: NameSpace,
      children: [
        {
          path: '',
          name: 'home',
          component: Home,
          meta: {
            // 要求登录
            requireLogin: true,
          },
        },
        // 重定向
        {
          path: 'index',
          name: 'home',
          component: Home,
          meta: {
            // 要求登录
            requireLogin: true,
          },
        },
        {
          path: 'record',
          name: 'record',
          component: Record,
          meta: {
            // 要求登录
            requireLogin: true,
          },
        },
        {
          path: 'modify-pwd',
          name: 'modify-pwd',
          component: ModifyPwd,
          meta: {
            // 要求登录
            requireLogin: true,
          },
        },
        {
          path: 'bind-phone',
          name: 'bind-phone',
          component: BindPhone,
          meta: {
            // 要求登录
            requireLogin: true,
          },
        },
        {
          path: 'login',
          name: 'login',
          component: () => import('./views/Login.vue'),
        },
        {
          path: 'register',
          name: 'register',
          component: () => import('./views/Register.vue'),
        },
        {
          path: 'visitor-register',
          name: 'visitorRegister',
          component: () => import('./views/VisitorRegister.vue'),
        },
        {
          path: 'invitation/:lang/:invitationId',
          name: 'invitation',
          component: () => import('./views/Invitation.vue'),
        },
      ],
    },
    {
      path: '*',
      name: 'notfound',
      component: () => import('./views/NotFound.vue'),
    },
  ],
});

router.afterEach((route) => {
  // 设置标题
  document.title = route.meta.title || 'U-CloudVision优云视';
});

export default router;
