<template>
  <div>
    <NavBar :title="$t('common.modifyPwd')" @click-left="goBackHandler" :leftArrow="true" :left-text="peopleName"></NavBar>
    <div class="form-wrap border-form">
      <div class="border-item">
        <span class="label">{{$t('common.oldPwd')}}</span>
        <cube-input
          v-model="model.old_passwd"
          :placeholder="$t('common.oldPwdPlaceholder')"
          type="password"
          :maxlength="maxlength"
          :autofocus="true"
          :eye="eye"></cube-input>
      </div>
      <div class="border-item">
        <span class="label">{{$t('common.newPwd')}}</span>
        <cube-input
          v-model="model.new_passwd_a"
          :placeholder="$t('common.newPwdPlaceholder')"
          type="password"
          :maxlength="maxlength"
          :autofocus="true"
          :eye="eye"></cube-input>
      </div>
      <div class="border-item">
        <span class="label nowrap">{{$t('common.repeatPwd')}}</span>
        <cube-input
          v-model="model.new_passwd_b"
          :placeholder="$t('common.repeatPwdPlaceholder')"
          type="password"
          :maxlength="maxlength"
          :autofocus="true"
          :eye="eye"></cube-input>
      </div>
      <div class="submit-wrap">
        <UButton
          buttonType="submit"
          size="large"
          @click="submitHandler">{{$t('common.modify')}}</UButton>
      </div>
    </div>
  </div>
</template>

<script>
import NavBar from '@/components/NavBar.vue';
import UButton from '@/components/U-Button.vue';
import { trimData } from '../utils/utils';

const apis = {
  pwd: '/h5/password',
};

export default {
  data() {
    return {
      model: {
        old_passwd: '',
        new_passwd_a: '',
        new_passwd_b: '',
      },
      maxlength: 16,
      eye: {
        open: false,
        reverse: false,
      },
    };
  },
  methods: {
    goBackHandler() {
      this.$router.go(-1);
    },
    checkData(data) {
      const { old_passwd: old, new_passwd_a: newA, new_passwd_b: newB } = data;
      const rules = [
        [!old, this.$t('common.oldPwdRequired')],
        [!newA, this.$t('common.newPwdRequired')],
        [newA !== newB, this.$t('common.pwdNotEqual')],
      ];
      return rules.every((r) => {
        if (r[0]) {
          this.showToastType(r[1]);
          return false;
        }
        return true;
      });
    },
    submitHandler() {
      const data = trimData(this.model);
      if (this.checkData(data)) {
        console.log('valid');
        this.http.post(apis.pwd, data).then((res) => {
          if (res.code === 1000) {
            this.modifySuccess();
          } else {
            this.showToastType(res.msg ? `${res.msg}${res.reason ? `:${res.reason}` : ''}` : this.$t('common.modifyPwdFailed'));
          }
        }).catch(() => {
          this.showToastType(this.$t('common.modifyPwdFailed'));
        });
      }
    },
    modifySuccess() {
      this.$createDialog({
        type: 'alert',
        icon: 'cubeic-alert',
        title: this.$t('common.modifyPwdSuccess'),
        onConfirm: () => {
          this.$storage.removeItem('user', false);
          this.$router.push('login');
        },
      }).show();
    },
    showToastType(txt, type = 'error') {
      const toast = this.$createToast({
        txt,
        type,
      });
      toast.show();
    },
  },
  computed: {
    peopleName() {
      const user = this.$storage.getItem('user', false) || {};
      return user.peopleName || '';
    },
  },
  components: {
    NavBar,
    UButton,
  },
};
</script>

<style lang="stylus" scoped>
  @import "../assets/styles/common.styl";
  @import "../assets/styles/border-form.styl";
  .form-wrap {
    margin: 0 auto;
    padding: 19px 18px 0;
    .submit-wrap {
      margin-top: 120px;
      margin-left: 18px;
    }
  }
</style>
