<template>
  <div>
    <NavBar :title="telephone ? $t('common.modifyPhone') : $t('common.bindPhone')" @click-left="goBackHandler" :leftArrow="true" :left-text="peopleName"></NavBar>
    <div class="form-wrap border-form">
      <div class="border-item">
        <span class="label">{{$t('common.telephoneNumber')}}</span>
        <cube-input
          v-model="model.telephone"
          :placeholder="$t('common.telephoneNumberPlaceholder')"
          :maxlength="20"
          :autofocus="true"></cube-input>
      </div>
      <div class="submit-wrap">
        <UButton
          buttonType="submit"
          size="large"
          @click="submitHandler">{{$t('common.submit')}}</UButton>
      </div>
    </div>
  </div>
</template>

<script>
import NavBar from '@/components/NavBar.vue';
import UButton from '@/components/U-Button.vue';
import { trimData } from '../utils/utils';

const apis = {
  phone: '/h5/bindPhone',
};
export default {
  data() {
    return {
      model: {
        telephone: '',
      },
    };
  },
  mounted() {
    this.model.telephone = this.telephone || '';
  },
  methods: {
    goBackHandler() {
      this.$router.go(-1);
    },
    checkData(data) {
      const { telephone } = data;
      const rules = [
        [!telephone, this.$t('common.telephoneNumberPlaceholder')],
        [!(/^([()+-\s0-9]){6,20}$/.test(telephone)), this.$t('common.telephoneError')],
      ];
      return rules.every((r) => {
        if (r[0]) {
          this.showToastType(r[1]);
          return false;
        }
        return true;
      });
    },
    submitHandler() {
      const data = trimData(this.model);
      const errMsg = this.telephone ? this.$t('common.modifyFailed') : this.$t('common.bindFailed');
      if (this.checkData(data)) {
        this.http.post(apis.phone, data).then((res) => {
          if (res.code === 1000) {
            this.showToastType(this.telephone ? this.$t('common.modifySuccess') : this.$t('common.bindSuccess'), 'correct');
            const user = { ...this.user };
            user.telephone = data.telephone;
            this.$storage.setItem('user', user, false);
          } else {
            this.showToastType(res.msg ? `${res.msg}${res.reason ? `:${res.reason}` : ''}` : errMsg);
          }
        }).catch(() => {
          this.showToastType(errMsg);
        });
      }
    },
    showToastType(txt, type = 'error') {
      const toast = this.$createToast({
        txt,
        type,
      });
      toast.show();
    },
  },
  computed: {
    user() {
      return this.$storage.getItem('user', false) || {};
    },
    peopleName() {
      return this.user.peopleName || '';
    },
    telephone() {
      return this.user.telephone || '';
    },
  },
  components: {
    NavBar,
    UButton,
  },
};
</script>

<style lang="stylus" scoped>
  @import "../assets/styles/common.styl";
  @import "../assets/styles/border-form.styl";
  .form-wrap {
    margin: 0 auto;
    padding: 19px 18px 0;
    .submit-wrap {
      margin-top: 120px;
      margin-left: 18px;
    }
  }
</style>
