import moment from 'moment';

export function formatStartDate(d, pattern = 'YYYY-MM-DD') {
  return d ? `${moment(d).format(pattern)} 00:00:00` : '';
}

export function formatEndDate(d, pattern = 'YYYY-MM-DD') {
  return d ? `${moment(d).format(pattern)} 23:59:59` : '';
}

export function dateFormat(d, pattern = 'YYYY-MM-DD') {
  return d ? moment(d).format(pattern) : '';
}

export function getFormData(data) {
  const formData = new FormData();
  const keys = Object.keys(data);
  keys.forEach((it) => {
    formData.append(it, data[it]);
  });
  return formData;
}

export function trimData(data) {
  const obj = {};
  const keys = Object.keys(data);
  keys.forEach((it) => {
    let v = data[it];
    if (typeof v === 'string') {
      v = v.trim();
    }
    obj[it] = v;
  });
  return obj;
}
