/**
 * 注册方法
 * @export {Function} install
 * @param {Object} Vue Vue 对象
 */
// 导出一个install方法，用于注册插件
export default function install(Vue) {
  // vue原型上添加getSOH方法
  Vue.prototype.getSOH = function getStorageOrHttp(key, url, param, type = 'get') {
    const storageKey = ['http', key];
    // 返回一个promise实例
    return new Promise((resolve, reject) => {
      const storage = this.$storage;
      // 获取缓存内容
      const response = storage.getItem(storageKey, false);
      // 如果没有缓存，reject，否则resolve
      if (!response) {
        reject(storage);
      }
      resolve(response);
    }).catch((storage) => {
      // rejected的promise会被捕获
      // 重新请求
      this.http[type](url, param).then((res) => {
        // 将结果设置为缓存，并返回该缓存
        storage.setItem(storageKey, res, false);
        return storage.getItem(storageKey, false);
      });
    });
  };
}
