      default: 'large',
    },
    disabled: {
      type: Boolean,
      default: false,
    },
    outline: Boolean,
    inline: Boolean,
    light: Boolean,
    primary: Boolean,
  },
  methods: {
    clickHandler() {
      this.$emit('click');
    },
  },
};
</script>

