<template>
  <div>
    <div class="daterange-picker">
      <div @click="showDateSegmentPicker">
      <cube-input
        class="daterange"
        :class="[radius ? 'radius': '']"
        :placeholder="placeholder || this.$t('common.startDateToEndDate')"
        :value="selected[0] ? `${dateFormat(selected[0], this.format)}${split || this.$t('common.to')}${dateFormat(selected[1], this.format)}`: ''"
        readonly>
          <svg class="icon" aria-hidden="true" v-if="showIcon" slot="prepend">
            <use xlink:href="#icon-rili"></use>
          </svg>
      </cube-input>
      </div>
      <span class="operate-wrap">
        <a href="javascript:;" @click="onReset" class="link" v-if="showToday">{{$t('common.today')}}</a>
        <a href="javascript:;" @click="onClear" class="link" v-if="showClear">{{$t('common.clear')}}</a>
      </span>
    </div>
  </div>
</template>

<script>
import { dateFormat } from '../utils/utils';

const dateSegmentData = [
  {
    is: 'cube-date-picker',
    title: '开始日期',
    columnCount: 3,
  },
  {
    is: 'cube-date-picker',
    title: '结束日期',
    columnCount: 3,
  },
];

export default {
  data() {
    return {
      selected: [],
    };
  },
  props: {
    value: {
      type: Array,
      default: () => [],
    },
    placeholder: {
      type: String,
      default: '',
    },
    showToday: {
      type: Boolean,
      default: true,
    },
    showClear: {
      type: Boolean,
      default: false,
    },
    startLabel: {
      type: String,
      default: '',
    },
    endLabel: {
      type: String,
      default: '',
    },
    columnCount: {
      type: Number,
      default: 3,
    },
    format: {
      type: String,
      default: 'YYYY-MM-DD',
    },
    showIcon: {
      type: Boolean,
      default: true,
    },
    radius: {
      type: Boolean,
      default: true,
    },
    split: {
      type: String,
      default: '',
    },
    minStart: Date,
    minEnd: Date,
  },
  created() {
    this.selected = this.value;
    dateSegmentData[0].title = this.startLabel || this.$t('common.startDate');
    dateSegmentData[1].title = this.endLabel || this.$t('common.endDate');
    dateSegmentData[0].min = this.minStart;
    dateSegmentData[1].min = this.minEnd;
    dateSegmentData[0].value = this.selected[0];
    dateSegmentData[1].value = this.selected[1];
    dateSegmentData[0].columnCount = this.columnCount;
    dateSegmentData[1].columnCount = this.columnCount;
  },
  mounted() {
    this.dateSegmentPicker = this.$createSegmentPicker({
      data: dateSegmentData,
      onSelect: (selectedDates) => {
        dateSegmentData[0].value = selectedDates[0];
        dateSegmentData[1].value = selectedDates[1];
        this.selected = selectedDates;
        this.$set(this.selected, selectedDates);
        this.$emit('input', selectedDates);
      },
      onNext: (i, selectedDate) => {
        dateSegmentData[1].min = selectedDate;
        if (i === 0) {
          this.dateSegmentPicker.$updateProps({
            data: dateSegmentData,
          });
        }
      },
    });
  },
  methods: {
    showDateSegmentPicker() {
      this.dateSegmentPicker.show();
    },
    onClear() {
      this.setValue([]);
    },
    onReset() {
      this.setValue([new Date(), new Date()]);
    },
    setValue(value) {
      this.selected = value;
      this.$emit('input', value);
    },
    dateFormat(d, format) {
      return dateFormat(d, format);
    },
  },
};
</script>

