@import "../assets/styles/common.styl";
.daterange-picker {
  text-align: left;
  .daterange {
    padding-left: 15px;
    box-sizing: border-box;
    width: 100%;
    font-size: $size-normal;
    height: 26px;
    line-height: 1;

    // border: 1PX solid $border-color;
    display: inline-flex;
    align-items: center;
  }
  .radius {
    >>> &::after {
      border-radius: 50px;
    }
  }

  >>> .cube-input-field {
    padding: 0;
  }

  .operate-wrap {
    .link {
      line-height: 26px;
      margin: 0 5px;
      vertical-align: middle;
    }
  }

  .icon {
    color: $color-blue-gray;
    margin-right: 12px;
  }
}
</style>
