import Vue from 'vue';
import CubeUI from 'cube-ui';
import moment from 'moment';
// cube-ui 内置英文语言包
import enUSMessages from 'cube-ui/src/locale/lang/en-US';
import zhCNMessages from 'cube-ui/src/locale/lang/zh-CN';
import VueI18n from 'vue-i18n';
import App from './App.vue';
import vuexStorage from './utils/vuex-storage';
import router from './router';
import './registerServiceWorker';
import httpPlugin, { http, addRequestInterceptor, addResponseInterceptor } from './utils/http';
import '@babel/polyfill';
// 如果flexiable放在index.html头部加载有兼容性问题（华为平板字体缩放方案不能生效）
import './utils/flexiable';
// 自己引入繁体中文语言包
import zhTWMessages from './locales/cube-ui/zh_TW';
import zhCN from './locales/zh-CN';
import enUS from './locales/en-US';
import zhTW from './locales/zh-TW';

const Locale = CubeUI.Locale;
// let currLang = localStorage.getItem('lang');
// 写死
let currLang = 'zh-CN';
function getBLang() {
  const browserLanguage = window.navigator.language.toLowerCase();
  let res = 'en-US';
  if (browserLanguage.indexOf('zh') === 0) {
    res = 'zh-TW';
    if (['zh', 'zh-cn', 'zh-hans-cn'].indexOf(browserLanguage) !== -1) {
      res = 'zh-CN';
    }
  }
  localStorage.setItem('lang', res);
  return res;
}

Vue.use(CubeUI);
Vue.use(VueI18n);
if (!currLang) {
  currLang = getBLang();
}
switch (currLang) {
  case 'en-US':
    Locale.use('en-US', enUSMessages);
    break;
  case 'zh-TW':
    Locale.use('zh-TW', zhTWMessages);
    break;
  default:
    Locale.use('zh-CN', zhCNMessages);
}
const i18n = new VueI18n({
  // 语言标识
  locale: currLang,
  messages: {
    'zh-CN': zhCN,
    'en-US': enUS,
    'zh-TW': zhTW,
  },
});
Vue.filter('dateformat', (dataStr, pattern = 'YYYY-MM-DD') => moment(dataStr).format(pattern));
// 注册插件
Vue.use(httpPlugin);

Vue.config.productionTip = false;

const getUser = function getUser($storage) {
  return new Promise((resolve) => {
    let userInfo = $storage.getItem('user', false);
    if (userInfo) {
      resolve(userInfo);
    } else {
      // 手动刷新时，vuex存储会丢失，先请求一遍用户信息，判断是否已登录
      // 已登录，可直接根据接口拿到用户信息，再存储到vuex中
      http.get('/h5/info').then((res) => {
        if (res.code === 1000) {
          userInfo = {
            peopleName: res.data.people_name,
            telephone: res.data.telephone,
          };
          $storage.setItem('user', userInfo, false);
          resolve(userInfo);
        } else {
          console.log(`${res.msg}${res.reason ? `:${res.reason}` : ''}`);
          resolve({});
        }
      }).catch((e) => {
        console.log(e);
        resolve({});
      });
    }
  });
};

const store = vuexStorage(Vue, router, [], ($storage, to, from) => new Promise((resolve, reject) => {
  const { name, meta } = to;
  const { requireLogin } = meta;
  if (name === 'login' || name === 'register') {
    resolve($storage, to, from);
    // const user = $storage.getItem('user', false);
    // if (!(user && user.peopleName)) {
    //   resolve($storage, to, from);
    // } else {
    //   // 若已登录，路由是登录或注册页的，跳到首页
    //   router.push('/');
    // }
  } else if (!requireLogin) {
    resolve($storage, to, from);
  } else {
    getUser($storage).then((user) => {
      const needLogin = !(user && user.peopleName);
      if (needLogin) {
        router.push('login');
        reject(new Error('need login!'));
      } else {
        resolve($storage, to, from);
      }
    });
  }
}));

// request前自动添加api配置
addRequestInterceptor(
  (config) => {
    config.url = `/api${config.url}`;
    return config;
  },
  (error) => {
    console.log(error);
    return Promise.reject(error);
  },
);

// 返回response前处理
addResponseInterceptor(
  (response) => {
    // 在这里统一前置处理请求响应
    if (Number(response.status) !== 200) {
      // 全局notify有问题，还是自己处理吧
      return Promise.reject(response.data);
    }
    return Promise.resolve(response.data);
  },
  (error) => {
    const data = error.response;
    if (data && data.status === 401) {
      Vue.prototype.$storage.removeItem('user', false);
      router.push('login');
    }
    console.log(error);
    Promise.reject(error || '出错了');
  },
);

new Vue({
  i18n,
  router,
  store,
  render: h => h(App),
}).$mount('#app');
