<template>
  <div>
    <!-- tab-title -->
    <cube-tab-bar v-model="selectedValue" @change="changeTabHandler">
      <cube-tab :label="it.label" :value="it.value" v-for="it in tabs" :key="it.value" class="tab-title">
        <UButton size="middle" :primary="it.value ==selectedValue" outline >{{it.label}}</UButton>
      </cube-tab>
    </cube-tab-bar>
    <!-- tab-panel -->
    <cube-tab-panels v-model="selectedValue">
      <cube-tab-panel :label="tabs[0].label" :value="tabs[0].value">
        <div class="form-wrap border-form">
          <div class="border-item">
            <span class="label">{{$t('common.userName')}}</span>
            <cube-input v-model="model.peopleName" :placeholder="$t('common.userNamePlaceholder')" :autofocus="true" :maxlength="30"></cube-input>
          </div>
          <div class="border-item">
            <span class="label">{{$t('common.telephone')}}</span>
            <cube-input v-model="model.telephone" :placeholder="$t('common.telephonePlaceholder')" :maxlength="20"></cube-input>
          </div>
          <div class="border-item">
            <span class="label">{{$t('common.reason')}}</span>
            <cube-input v-model="model.reason" :placeholder="$t('common.reasonPlaceholder')" :maxlength="50"></cube-input>
          </div>
          <div class="border-item">
            <span class="label">{{$t('common.visitTime')}}</span>
            <DateRangePicker
              ref="datepiacker"
              v-model="model.date"
              :showClear="false"
              :showIcon="false"
              :showToday="false"
               :placeholder="$t('common.visitTimePlaceholder')"
               :columnCount="5"
               :radius="false"
               :minStart="new Date()"
               :minEnd="new Date()"
               split="~"
               format="YYYY-MM-DD HH:mm"></DateRangePicker>
          </div>
          <div class="submit-wrap">
            <UButton
              buttonType="submit"
              size="large"
              @click="submitHandler">{{$t('common.generateLinkAndShare')}}</UButton>
          </div>
        </div>
      </cube-tab-panel>
      <!-- 我的访客 -->
      <cube-tab-panel :label="tabs[1].label" :value="tabs[1].value">
        <div class="view-wrapper">
          <cube-scroll ref="scroll" :data="dataList" :options="options" @pulling-down="onPullingDown" @pulling-up="onPullingUp" class="scroll-list">
            <!-- 列表 -->
            <template v-if="dataList.length">
              <section class="record-item" v-for="(item) in dataList" :key="item.id">
                <div :class="isEn ? 'avatar-en' : 'avatar'" :style="item.thumbnail ? {backgroundImage: 'url(' + (item.thumbnail) + ')'} :{}" @click="() => goInvitation(item)">
                  {{item.inviter_status === 2 ? $t('common.toConfirm') : ''}}
                </div>
                <section class="info">
                  <section class="info-top">
                    <div class="info-top-left">
                      <div class="name space"><span class="label">{{$t('common.userName')}}：</span>{{item.people_name}}</div>
                      <div class="phone space"><span class="label">{{$t('common.telephone')}}：</span>{{item.telephone}}</div>
                    </div>
                    <a class="link" @click="() => delConfirm(item.people_id)">{{$t('common.delete')}}</a>
                  </section>
                  <div class="reason space"><span class="label">{{$t('common.reason')}}：</span>{{item.reasons_for_access}}</div>
                  <div class="visit-time space word-break">
                    <span class="label">{{$t('common.visitTime')}}：</span>
                    <span>{{item.start_time | dateformat('YYYY.MM.DD HH:mm')}}~{{item.end_time | dateformat('YYYY.MM.DD HH:mm')}}</span>
                    </div>
                </section>
              </section>
            </template>
            <template v-else>
              <!-- 空数据 -->
              <NoData>{{$t('common.noVisitors')}}</NoData>
            </template>
          </cube-scroll>
        </div>
      </cube-tab-panel>
    </cube-tab-panels>

    <cube-popup type="my-popup" ref="myPopup" :mask-closable="true">
      <div class="qrcode-wrap">
      <p class="tit">{{$t('common.invitation')}}</p>
      <qrcode :value="this.qrcodeUrl" :options="{ width: 200 }" tag="img"></qrcode>
      <p class="mark">{{$t('common.invitationMark')}}</p>
      </div>
    </cube-popup>
  </div>
</template>

<script>
import VueQrcode from '@chenfengyuan/vue-qrcode';
import UButton from '@/components/U-Button.vue';
import NoData from '@/components/NoData.vue';
import DateRangePicker from '@/components/DateRangePicker.vue';
import { trimData, dateFormat } from '../../utils/utils';


const apis = {
  visitors: '/h5/visitors',
  invitation: '/h5/invitation',
};

export default {
  data() {
    return {
      pageSize: 50,
      totalNumber: 1,
      totalPage: 1,
      pageNumber: 0,
      dataList: [],
      model: {
        peopleName: '',
        telephone: '',
        reason: '',
        date: [],
      },
      selectedValue: 'visitor',
      qrcodeUrl: 'qrcode',
    };
  },
  components: {
    UButton,
    NoData,
    DateRangePicker,
    qrcode: VueQrcode,
  },
  methods: {
    goInvitation(item) {
      const id = item.people_id;
      if (item.inviter_status === 2) {
        const lang = localStorage.getItem('lang') || 'zhCN';
        this.$router.push({ name: 'invitation', params: { invitationId: id, lang } });
      }
    },
    onGetData() {
      this.totalNumber = 1;
      this.totalPage = 1;
      this.pageNumber = 1;
      this.loadData(true);
    },
    // 获取数据
    loadData(replace) {
      const {
        pageNumber, pageSize,
      } = this;
      const pageData = {
        page_number: pageNumber,
        page_size: pageSize,
      };
      const formData = {
        ...pageData,
      };
      this.http.get(apis.visitors, {
        params: formData,
      }).then((res) => {
        if (res.code === 1000) {
          const items = res.data.peoples;
          this.totalNumber = res.data.total_number;
          this.pageNumber = res.data.page_number;
          this.totalPage = Math.ceil(this.totalNumber / this.pageSize);
          this.dataList = replace ? items : [...this.dataList, ...items];
        } else {
          this.$refs.scroll.forceUpdate();
          console.log(`${res.msg}${res.reason ? `:${res.reason}` : ''}`);
        }
      }).catch((e) => {
        if (this.$refs.scroll) {
          this.$refs.scroll.forceUpdate();
        }
        console.log(e);
      });
    },
    // 触发下拉刷新
    onPullingDown() {
      // console.log('下拉刷新');
      this.totalNumber = 1;
      this.totalPage = 1;
      this.pageNumber = 1;
      this.loadData(true);
    },
    // 触发上拉加载
    onPullingUp() {
      // console.log('上拉加载');
      if (this.totalPage <= this.pageNumber) {
        this.$refs.scroll.forceUpdate();
      } else {
        this.pageNumber = this.pageNumber + 1;
        this.loadData();
      }
    },
    showToastType(txt, type = 'error') {
      const toast = this.$createToast({
        txt,
        type,
      });
      toast.show();
    },
    reset() {
      this.model = {
        peopleName: '',
        telephone: '',
        reason: '',
        date: [],
      };
      this.$refs.datepiacker.onClear();
    },
    checkData(data) {
      const {
        peopleName,
        telephone,
        reason,
        date,
      } = data;
      const rules = [
        [!peopleName, this.$t('common.userNameRequired')],
        [!telephone, this.$t('common.telephoneNumberRequired')],
        [!(/^([()+-\s0-9]){6,20}$/.test(telephone)), this.$t('common.telephoneError')],
        [!reason, this.$t('common.visitReasonRequired')],
        [(!date) || date.length < 2, this.$t('common.visitTimeRequired')],
      ];
      return rules.every((r) => {
        if (r[0]) {
          this.showToastType(r[1]);
          return false;
        }
        return true;
      });
    },
    changeTabHandler(v) {
      if (v === 'visitor') {
        this.onGetData();
      }
    },
    submitHandler() {
      const data = trimData(this.model);
      if (this.checkData(data)) {
        console.log('valid');
        const {
          peopleName,
          telephone,
          reason,
          date,
        } = data;
        const formData = {
          people_name: peopleName,
          telephone,
          reasons_for_access: reason,
          start_time: dateFormat(date[0], 'YYYY-MM-DD HH:mm:00'),
          end_time: dateFormat(date[1], 'YYYY-MM-DD HH:mm:00'),
        };
        this.http.post(apis.invitation, formData).then((res) => {
          if (res.code === 1000) {
            const invitationId = res.data.invitation_id;
            const lang = localStorage.getItem('lang') || 'zhCN';
            const resolved = this.$router.resolve(`invitation/${lang}/${invitationId}`);
            const href = resolved.href;
            this.qrcodeUrl = `${window.location.origin}/${href}`;
            this.showPopup('myPopup');
            this.reset();
          } else {
            this.showToastType(res.msg ? `${res.msg}${res.reason ? `:${res.reason}` : ''}` : this.$t('common.inviteFailed'));
          }
        }).catch(() => {
          this.showToastType(this.$t('common.inviteFailed'));
        });
      }
    },
    showPopup(refName) {
      const component = this.$refs[refName];
      component.show();
    },
    delConfirm(id) {
      const that = this;
      this.$createDialog({
        type: 'confirm',
        icon: 'cubeic-alert',
        title: that.$t('common.confirmToDeleteVisitor'),
        content: '',
        confirmBtn: that.$t('common.confirm1'),
        cancelBtn: that.$t('common.cancel'),
        onConfirm: () => {
          this.doDelete(id);
        },
      }).show();
    },
    doDelete(id) {
      this.http.delete(apis.visitors, {
        data: {
          people_id: id,
        },
      }).then((res) => {
        if (res.code === 1000) {
          this.showToastType(this.$t('common.deleteSuccess'));
          this.onGetData();
        } else {
          this.showToastType(res.msg ? `${res.msg}${res.reason ? `:${res.reason}` : ''}` : this.$t('common.deleteFailed'));
        }
      }).catch((e) => {
        this.showToastType(this.$t('common.deleteFailed'));
        console.log(e);
      });
    },
  },
  mounted() {
    // 挂载
    this.loadData();
  },
  computed: {
    // 列表加载选项，空数据时只有下拉刷新而无上拉加载
    options() {
      const { pullDownRefresh, pullUpLoad } = this;
      return this.dataList.length ? { pullDownRefresh, pullUpLoad } : { pullDownRefresh };
    },
    locale() {
      return this.$i18n.locale;
    },
    isEn() {
      return this.locale === 'en-US';
    },
    // i18n不支持props和data，需要放在computed里
    pullUpLoad() {
      return {
        threshold: 0,
        txt: {
          more: this.$t('common.pullUptoLoadMore'),
          noMore: this.$t('common.noMoreData'),
        },
      };
    },
    pullDownRefresh() {
      return {
        threshold: 90,
        stop: 40,
        txt: this.$t('common.refreshSuccess'),
      };
    },
    tabs() {
      return [{
        label: this.$t('common.inviteVisitor'),
        value: 'invite',
      }, {
        label: this.$t('common.myVisitors'),
        value: 'visitor',
      }];
    },
  },
};
</script>

<style lang="stylus" scoped>
@import "../../assets/styles/common.styl";
@import "../../assets/styles/border-form.styl";
.tab-title {
  padding: 0;
}

.view-wrapper{
  height: calc(100vh - 160px);
  margin-right: -16px;
  .record-item {
    display: flex;
    align-items: center;
    height: 118px;
    padding-top: 22px;
    box-sizing: border-box;
    font-size: $size-small;
    color: $color-dark-gray;
    margin-right: 16px;
    align-items: flex-start;
    overflow: hidden;
    border-bottom: 1px solid $border-color3;
    .avatar, .avatar-en {
      width: 75px;
      height: 75px;
      line-height: 75px;
      margin-right: 10px;
      background-size: cover;
      // 避免拉伸
      flex-shrink: 0;
      background-color: #e6e6e6;
      color: #b3b3b3;
      font-size: 18px;
    }
    .avatar-en {
      font-size: 12px;
    }
    .info {
      text-align: left;
      line-height: 16px;
      display: flex;
      overflow: hidden;
      flex-direction: column;
      &-top {
        flex-v-center();
        justify-content: space-between;
        .link {
          flex-shrink: 0;
          padding: 10px;
        }
        &-left {
          width: 200px;
        }
      }
      .space  {
        margin-bottom: 4px;
      }
    }
    .name,.phone {
      ellipsis(200px);
    }
    .reason {
      clamp(2)
    }
    .label {
      color: #999;
    }
    .visit-time {
      display: flex;
      flex-wrap: wrap;
    }
  }
}


.scroll-list {
  >>> .cube-pulldown-loaded, >>> .before-trigger {
    color: $color-light-gray;
    font-size: $size-normal;
  }
}

.form-wrap {
  margin: 0 auto;
  padding-top: 19px;
  .submit-wrap {
    margin-top: 120px;
    margin-left: 18px;
    // ios：防止底部tab遮盖页面内容
    padding-bottom: 60px;
  }
}

.qrcode-wrap {
  background: $color-white;
  padding: 30px 20px 20px;
  .tit {
    font-size: $size-large;
    line-height: 22px;
    color: $color-dark-gray;
  }
  .mark {
    font-size: $size-normal;
    color: $color-light-gray;
  }
}

</style>
